.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh; 
}

.image-gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    max-width: 680px; 
    margin: 0 auto; 
    margin-top: 20px;
}

.image-gallery img {
    width: 150px; 
    height: 150px;
    margin-right: 20px; 
    margin-bottom: 20px; 
    box-sizing: border-box;
    vertical-align: top; 
}

.pagination {
  display: flex;
  justify-content: center; 
  align-items: center;
  margin: 20px 0; 
}

.pagination button {
  background-color: #007bff; 
  color: white;
  border: none;
  padding: 5px 10px;
  margin: 0 5px;
  border-radius: 5px;
  cursor: pointer;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination span {
  margin: 0 10px;
}

.upload-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.file-input-label {
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  cursor: pointer;
  background-color: #f8f9fa;
  margin-bottom: 10px;
}

.image-preview img {
  max-width: 100px; 
  max-height: 100px;
  margin: 5px; 
  border: 1px solid #ddd;
  border-radius: 5px;
}

.upload-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  margin-top: 10px;
}

.header {
  font-weight: bold; 
  font-size: 32px; 
  position: absolute; 
  top: 10%;
  left: 0;
  right: 0;
  text-align: center; 
}